import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import { deepClone } from '@/until/index';
let priceConfig = class priceConfig extends Vue {
    constructor() {
        super(...arguments);
        this.className = '业务配置';
        this.validity = {};
        this.valid = undefined;
        this.model = {
            '0': 0,
            '1': 0
        };
        this.schema = {
            groups: [
                {
                    fields: [
                        {
                            type: 'input',
                            modelKey: '0',
                            label: '帮我取',
                            props: {
                                placeholder: '请输入基础费用(元)',
                                type: 'text'
                            },
                            rules: {
                                required: true,
                                custom: (val) => {
                                    return val >= 0;
                                }
                            },
                            messages: {
                                custom: '费用需要大于等于0元'
                            },
                            // validating when blur
                            trigger: 'blur'
                        },
                        {
                            type: 'input',
                            modelKey: '1',
                            label: '帮我做',
                            props: {
                                placeholder: '请输入基础费用(元)',
                                type: 'text'
                            },
                            rules: {
                                required: true,
                                custom: (val) => {
                                    return val >= 0;
                                }
                            },
                            messages: {
                                custom: '费用需要大于等于0元'
                            },
                            // validating when blur
                            trigger: 'blur'
                        }
                    ]
                },
                {
                    fields: [
                        {
                            type: 'submit',
                            label: '保存'
                        }
                    ]
                }
            ]
        };
        this.options = {
            scrollToInvalidField: true,
            layout: 'standard' // standard fresh
        };
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    submitHandler(e) {
        e.preventDefault();
        let feeObj = {};
        console.log(this.model);
        Object.keys(this.model).forEach((item) => {
            feeObj[item] = {};
            feeObj[item]['basicfee'] = parseFloat(this.model[item]);
        });
        this.saveSchoolInfo('basicfee', feeObj);
    }
    validateHandler(result) {
        this.validity = result.validity;
        this.valid = result.valid;
        console.log('validity', result.validity, result.valid, result.dirty, result.firstInvalidFieldIndex);
    }
    reset(e) {
        this.$refs.addForm.reset();
    }
    saveSchoolInfo(type, value) {
        this.$store.dispatch('updateSchoolInfo', {
            schoolId: this.schoolInfo.id,
            type: type,
            value: value
        }).then((e) => {
            if (e.success) {
                this.showToastType();
            }
            this.$store.commit('SET_SCHOOL_INFO_BY_TYPE', {
                type: type,
                value: value
            });
        });
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
    initModelData() {
        let basicfeeConfig = deepClone(this.schoolInfo.basicfee || {
            '0': {
                basicfee: 2
            },
            '1': {
                basicfee: 2
            }
        });
        let model = {};
        Object.keys(basicfeeConfig).forEach((item) => {
            model[item] = basicfeeConfig[item].basicfee;
        });
        this.model = model;
    }
    mounted() {
        this.initModelData();
    }
};
priceConfig = __decorate([
    Component({
        components: {
            page,
            pageView
        }
    })
], priceConfig);
export default priceConfig;
